.my-layout {
  min-height: 100vh;
}
.my-layout .my-header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 164px;
  padding: 4px 16px;
  line-height: 56px;
  background: #673ab7;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
  color: white;
  z-index: 999;
}
.my-layout .my-header .header-logo {
  font-size: 45px;
  margin-right: 12px;
}
.my-layout .my-header .header-img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to right top, rgba(100, 115, 201, 0.7), rgba(25, 32, 72, 0.7)), url(https://picsum.photos/1920/164?random);
  background-position: center center;
}
.my-layout .main-content {
  padding: 24px;
}
.my-layout .my-back-top {
  box-shadow: 0 3px 5px -1px rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.14), 0 1px 18px 0 rgba(0, 0, 0, 0.12);
  border-radius: 50%;
  right: 24px;
}
.my-layout .my-footer {
  padding: 0;
}
